  -moz-box-sizing: content-box;
  box-sizing: content-box;
  text-decoration: none;
}

#trp-controls-close:after {
  content: "ïŒµ";
}

#trp-controls-close:focus,
#trp-controls-close:hover {
  background: #fff;
  color: #0073aa;
  border-top-color: #0073aa;
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

#trp-preview {
