/*
 Translation Editors
 */

.trp-toggle-languages {
  margin-top: 10px;
  margin-bottom: 10px;
}

.trp-toggle-languages span,
.trp-next-previous-buttons,
.trp-add-media {
  cursor: pointer;
}

#trp-next-previous {
  margin: 5px 0 10px 0;
  display: flex;
  justify-content: space-between;
}

.trp-toggle-languages span,
.trp-next-previous-buttons span {
  font-size: initial;
}

.trp-toggle-languages span,
.trp-next-previous-buttons,
.trp-add-media {
  display: inline-block;
  text-decoration: none;
  font-size: 13px;
  line-height: 26px;
  height: 28px;
  margin: 0;
  padding: 0 10px 1px;
  cursor: pointer;
  border-width: 1px;
  border-style: solid;
  -webkit-appearance: none;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  white-space: nowrap;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  min-width: 85px;
}

.trp-toggle-languages span,
.trp-next-previous-buttons,
.trp-add-media {
  color: #555;
  border-color: #cccccc;
  background: #f7f7f7;
  -webkit-box-shadow: inset 0 1px 0 #fff, 0 1px 0 rgba(0, 0, 0, 0.08);
  box-shadow: inset 0 1px 0 #fff, 0 1px 0 rgba(0, 0, 0, 0.08);
  vertical-align: top;
}

.trp-toggle-languages span,
.trp-next-previous-buttons {
  vertical-align: baseline;
}

.trp-toggle-languages span:hover,
.trp-next-previous-buttons:hover,
.trp-next-previous-buttons:focus,
.trp-add-media:hover {
  background: #fafafa;
  border-color: #999;
  color: #222;
}

.trp-toggle-languages.trp-show-other-languages span:hover {
  background: #eee;
}

.trp-toggle-languages span,
.trp-next-previous-buttons:focus {
  -webkit-box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

.trp-toggle-languages.trp-show-other-languages span,
.trp-next-previous-buttons:active {
  background: #eee;
  border-color: #999;
  color: #333;
  -webkit-box-shadow: inset 0 2px 5px -3px rgba(0, 0, 0, 0.5);
  box-shadow: inset 0 2px 5px -3px rgba(0, 0, 0, 0.5);
}

.trp-toggle-languages {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#trp-save {
  margin-right: 10px;
  margin-top: 9px;
  margin-left: 10px;
  background: #0085ba;
  border-color: #0073aa #006799 #006799;
  -webkit-box-shadow: 0 1px 0 #006799;
  box-shadow: 0 1px 0 #006799;
  color: #fff;
  text-decoration: none;
  text-shadow: 0 -1px 1px #006799, 1px 0 1px #006799, 0 1px 1px #006799, -1px 0 1px #006799;
  display: inline-block;
  font-size: 13px;
  height: 28px;
  padding: 0 10px 1px;
  cursor: pointer;
  border-width: 1px;
  border-style: solid;
  -webkit-appearance: none;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  white-space: nowrap;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: inherit;
}

#trp-save:hover,
#trp-save-gettext:hover {
  background: #008ec2;
  border-color: #006799;
  color: #fff;
}

#trp-save:active,
#trp-save-gettext:active {
  background: #0073aa;
  border-color: #006799;
  -webkit-box-shadow: inset 0 2px 0 #006799;
  box-shadow: inset 0 2px 0 #006799;
}

#trp-translation-saved {
  padding-left: 20px;
  color: #008000;
  font-size: 14px;
  display: none;
}

/*
 * Loading animation
 */

.trp-ajax-loader {
  margin-bottom: -16px;
  margin-left: 20px;
  margin-right: 3px;
  display: inline-block;
  z-index: 50;
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(GradientType=1, StartColorStr='#E57EB149', EndColorStr='#E57EB149')";
  filter: progid:DXImageTransform.Microsoft.Gradient(GradientType=1, StartColorStr="#E57EB149", EndColorStr="#E57EB149");
  zoom: 1 !important;
}

.trp-ajax-loader .trp-spinner {
  width: 15px;
  height: 15px;
  border: 3px solid #a1b9e6;
  border-radius: 50%;
  border-left-color: transparent;
  border-right-color: transparent;
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

#trp-preview.trp-still-loading-strings:before {
  position: absolute;
  /* Sit on top of the page content */
  width: 100%;
  /* Full width (cover the whole page) */
  height: 30px;
  /* Full height (cover the whole page) */
  line-height: 30px;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(40, 177, 255, 0.8);
  /* Black background with opacity */
  z-index: 200;
  /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer;
  /* Add a pointer on hover */
  content: "The strings are not yet ready for translation. Try again in a moment...";
  color: #fff;
  text-align: center;
}

